<template>
  <div>
    <el-col :span="16">
      <common-wrapper>
        <common-table size="small" :flex="135">
          <template slot="table">
            <el-table ref="table" height="100%" :data="warnDeatilData" :default-sort = "{order: 'descending'}" stripe>
              <el-table-column type="index" :index="indexMethod" label="序号" sortable></el-table-column>
              <el-table-column prop="orgName" label="所属公司"></el-table-column>
              <el-table-column prop="lineName" label="所属线路"></el-table-column>
              <el-table-column prop="busPlateNum" label="车牌号"></el-table-column>
              <el-table-column prop="warnTypeName" label="检测类型"></el-table-column>
              <el-table-column label="位置/坐标">
                <template slot-scope="scope">
                      <el-popover
                        placement="right"
                        trigger="click"
                        >
                         <baidu-map-com :row="scope.row" :styleWH="styleWH" :ref="`popover-${scope.$index}`"></baidu-map-com>
                        <el-button  icon="el-icon-location-outline" circle slot="reference" @click="showPop(scope)" ></el-button>
                      </el-popover>
                </template>
              </el-table-column>
              <el-table-column :formatter="formatTime" prop="warnTime" label="触发时间">
                <template slot-scope="scope">
                  <el-tooltip
                    placement="bottom-end"
                  >
                    <div slot="content">{{ scope.row.warnTime|formatDate }}</div>
                    <div class="table-inline">{{ scope.row.warnTime|formatDate }}</div>
                  </el-tooltip>
                </template>
              </el-table-column>
              <el-table-column prop="warnMark" label="行为标记">
                <template slot-scope="scope">
                  <div :style="{color: changeColor(scope.row.warnMark)}">{{scope.row.warnMark}}</div>
                </template>
              </el-table-column>
              <el-table-column label="操作">
                <template slot-scope='scope'>
                  <el-button @click='seeWarn(scope.$index,scope.row)' type="text">查看</el-button>
                </template>
              </el-table-column>
            </el-table>
          </template>
          <template slot="pagination">
            <el-pagination
              background
              layout="total, prev, pager, next"
              :current-page.sync="DriverBehaviorPagination.pageNum"
              :page-size="DriverBehaviorPagination.pageSize"
              :total="DriverBehaviorPagination.total"
              @current-change="handlePageChange"
            ></el-pagination>
          </template>
        </common-table>
      </common-wrapper>
    </el-col>
    <el-col :span="8">
      <common-wrapper class="topPlay">
        <div class="player-content" v-if="videoPlayer">
          <div class="player">
            <video-player
              :options="playerOpts"
              @play="onPlayerPlay($event)"
              @pause="onPlayerPause($event)"
              @ended="onPlayerEnded($event)"
              @ready="playerReady($event)"
            ></video-player>
          </div>
        </div>
        <div class="player-content" v-if="jpegShow">
          <img v-bind:src="imgUrl" class="imgPosition"/>
        </div>
        <div class="player-content" v-if="noDataShow">
          <img src="../../../assets/images/noData.jpg" class="imgPosition"/>
        </div>
        <el-form label-width="100px">
          <el-form-item label="司机姓名："  class="dot">
            <span>{{warnRecordData.driverName}}</span>
          </el-form-item>
          <el-form-item label="运营线路：" class="dot">
            <span>{{warnRecordData.lineName}}</span>
          </el-form-item>
          <el-form-item label="所属公司：" class="dot">
            <span>{{warnRecordData.driverOrg}}</span>
          </el-form-item>
        </el-form>
        <el-form v-bind:class="{ shOrhd: isshOrhd }" :model="searchForm" label-width="100px" size="mini">
          <el-form-item style="display:none;">
            <input type="text" v-model="updateUuid">
            <p>{{updateUuid}}</p>
          </el-form-item>
          <el-form-item label="事件编号：" class="dot">
            <span>{{this.eventIndex}}</span>
          </el-form-item>
          <el-form-item label="行为标记：" prop="warnMark">
            <el-select v-model="searchForm.warnMark" placeholder="请选择">
              <el-option v-for="warn in warnHandle" :key="warn.id" :value="warn.id" :label="warn.value"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="large">
            <el-button type="primary" @click="changeStatus">更改标记</el-button>
          </el-form-item>
        </el-form>
      </common-wrapper>
    </el-col>
  </div>
</template>
<script>
import moment from 'moment'
import 'video.js/dist/video-js.css'
import CommonWrapper from '@/components/commonWrapper'
import CommonTable from '@/components/commonTable'
import {videoPlayer} from '@/components/videoPlayer'
import BaiduMapCom from '../PositionMap/index'
// api
import { warnsDriverGet, warnsWarndetailGet, warnWarntagUpdate, commonWarnMarksGet } from '@/api/fatigue'
export default {
  name: 'DriverBehaviorProcessing',
  components: {CommonWrapper, CommonTable, warnsDriverGet, BaiduMapCom, videoPlayer},
  props: {
    searchParams: Object,
    row: Object,
    isshOrhd: ''
  },
  data () {
    return {
      tableVisible: false,
      videoPlayer: true,
      jpegShow: true,
      noDataShow: true,
      imgUrl: '',
      warnDeatilData: [],
      warnRecordData: {},
      warnHandle: [],
      eventIndex: '',
      updateUuid: '',
      searchForm: {
        warnMark: ''
      },
      styleWH: {
        width: 400,
        height: 400
      },
      playerOpts: {
        autoplay: false,
        muted: true,
        playbackRates: [0.7, 1.0, 1.5, 2.0],
        sources: []
        // poster: '//vjs.zencdn.net/v/oceans.png'
      },
      searchDetailParams: {},
      warnTypeUuid: '',
      DriverBehaviorPagination: {
        pageNum: 1,
        pageSize: 10,
        total: 0
      }
    }
  },
  // 表格重新布局
  activated () {
    this.$refs.table.doLayout()
  },
  mounted () {
    this.eventIndex = 1
    // this.tableVisible = false
    // 初始化第一条疲劳检测详情记录
    warnsWarndetailGet({warnUuid: this.row.warnUuid}).then(res => {
      this.warnRecordData = res
      this.updateUuid = this.row.warnUuid
      if (this.warnRecordData.mediaType === '2') {
        this.jpegShow = false
        this.noDataShow = false
        this.videoPlayer = true
        // set video
        this.$set(this.playerOpts.sources, 0, {
          type: 'video/mp4',
          src: this.warnRecordData.url
        })
      } else if (this.warnRecordData.mediaType === '0') {
        this.jpegShow = true
        this.noDataShow = false
        this.videoPlayer = false
        this.imgUrl = this.warnRecordData.url
      } else {
        this.jpegShow = false
        this.videoPlayer = false
        this.noDataShow = true
      }
    })
    // 行为标记通用接口
    commonWarnMarksGet({}).then(res => {
      this.warnHandle = res
    })
    this.initWarnData()
  },
  methods: {
    initWarnData () {
      this.searchDetailParams = {
        warnUuid: this.row.warnUuid,
        pageNum: this.DriverBehaviorPagination.pageNum,
        pageSize: this.DriverBehaviorPagination.pageSize
      }
      this.getDriverBehaviorList(this.searchDetailParams)
    },
    // 疲劳监测详情页面列表
    getDriverBehaviorList (params) {
      warnsDriverGet(params).then(res => {
        this.warnDeatilData = res.list
        this.DriverBehaviorPagination.pageNum = res.pageNum
        this.DriverBehaviorPagination.pageSize = res.pageSize
        this.DriverBehaviorPagination.total = res.total
        // this.tableVisible = false
      })
    },
    // 将选中行数据生成疲劳检测详情记录
    seeWarn (index, row) {
      this.updateUuid = row.warnUuid
      this.eventIndex = (index + 1) + (this.DriverBehaviorPagination.pageNum - 1) * this.DriverBehaviorPagination.pageSize
      warnsWarndetailGet({warnUuid: this.updateUuid}).then(res => {
        this.warnRecordData = res
        if (this.warnRecordData.mediaType === '2') {
          this.jpegShow = false
          this.noDataShow = false
          this.videoPlayer = true
          // set video
          this.$set(this.playerOpts.sources, 0, {
            type: 'video/mp4',
            src: this.warnRecordData.url
          })
        } else if (this.warnRecordData.mediaType === '0') {
          this.jpegShow = true
          this.noDataShow = false
          this.videoPlayer = false
          this.imgUrl = this.warnRecordData.url
        } else {
          this.jpegShow = false
          this.videoPlayer = false
          this.noDataShow = true
        }
      })
    },
    formatTime (row) {
      return moment(row.warnTime).utcOffset(8).format('YYYY-MM-DD HH:mm:ss')
    },
    changeColor (color) {
      switch (color) {
        case '未处理':
          color = '#F56C6C'
          break
        case '误报忽略':
          color = '#909399'
          break
        case '已处理':
          color = '#67C23A'
          break
      }
      return color
    },
    // 行为处理
    changeStatus () {
      const params = {
        warnUuid: this.updateUuid,
        warnTypeUuid: this.searchForm.warnMark
      }
      if (params.warnTypeUuid === '') {
        this.$message({
          message: '请选择状态后提交',
          type: 'warning'
        })
      } else {
        warnWarntagUpdate(params).then(res => {
          this.$message({
            message: '状态修改成功',
            type: 'success'
          })
          this.searchForm.warnMark = ''
          this.initWarnData()
        })
      }
    },
    onPlayerPlay (player) {
      //   this.maxWindow(true)
    },
    onPlayerPause (palyer) {
      //   this.maxWindow(false)
    },
    onPlayerEnded (player) {
      //   this.maxWindow(false)
    },
    playerReady (player) {

    },
    maxWindow (state) {
      this.$store.dispatch('maxWindow', state)
      this.$store.dispatch('hideSidebar', state)
    },
    handlePageChange (pageNum) {
      const params = {warnUuid: this.row.warnUuid, pageNum: pageNum, pageSize: this.DriverBehaviorPagination.pageSize}
      this.getDriverBehaviorList(params)
    },
    // 生成列表序号
    indexMethod (index) {
      return (index + 1) + (this.DriverBehaviorPagination.pageNum - 1) * this.DriverBehaviorPagination.pageSize
    },
    showPop (scope) {
      const com = `popover-${scope.$index}`
      this.$refs[com].showMap()
    }
  },
  filters: {
    formatDate: (date) => {
      if (date) {
        return moment(date).utcOffset(8).format('YYYY-MM-DD HH:mm:ss')
      }
    }
  }
}
</script>
<style lang="scss" scoped>
  .shOrhd{
    display: none;
  }
  .topPlay{
    position: absolute;
    overflow: auto;
    height:97%;
    width:32%;
    .player-content {
      display: flex;
      flex-direction: column;
      width:100%;
      margin: 20px 0;
      align-items: center;
      box-sizing: border-box;
      background: darken($base-dark-color, 5%);
      .player{
        width:90%;
        max-width:1000px;
        margin: 25px 0;
        color: darken( $base-light-color, 10%);

        .video-player {
          box-shadow: 0 10px 35px rgba(0,0,0,.7)
        }
      }
      .imgPosition{
        width:100%;
        height:100%;
      }

    }
    .dot{
      border-bottom:1px dotted #d9d9d9;
    }
  }
  .table-inline{
    width:100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
</style>



